@import url('https://fonts.googleapis.com/css?family=Jaldi|Racing+Sans+One&display=swap');


body {
    margin: 0;
}

@media screen and (min-width: 1550px) {
    .bgOpacity {
    animation: bgOpacity 2s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
    }
    form#searchbar {
        grid-area: search;
        display: flex;
        justify-content: flex-start;
        margin-right: 100px;
        padding: 10px;
        flex-grow: 3;
    }
    form button {
        margin-left: 10px;
    }
    
    /* sub grid areas */
    ul#category {
        grid-area: category;
    }
    article#jobboard {
        grid-area: jobboard;
    }
    article.postClicked {
        display: grid;
        grid-template-areas:  
            "list jobpost";
    }
    div#jobboardImage {
        background-image: url('../../img/rectangle_24.png');
        background-size: 100% 100%;
        margin: 10px;
        margin-left: 100px;
        margin-right: 100px;
        padding: 20px;
    }
    
    li.joblists {
        color: black;
    }
    
    
    /* open job post (animation) */
    div#job-description-wrapper {
        position: absolute;
        overflow: hidden;
        width: 500px;
        height: 500px;
        right: 200px;
        bottom: -200px;
    }
    div#job-description > div {
        position: absolute;
        background-color: rgba(0, 255, 34, 0.265);
        border-radius: 1vw;
        right: -500px;
        width: 500px;
        animation: JobPostAnimation 1s;
        animation-iteration-count: 1;
        animation-fill-mode: forwards;
        z-index:2;
        word-wrap:break-word;
    }
    .active {
        background-color: crimson;
    }
    
    /* main grid areas */
    section#content {
        position: absolute;
        top: 0px;
        width: 100vw;
        display: grid;
        grid-template-areas: 
            "category search"
            "jobboard jobboard";
        grid-template-rows: 1fr;
        grid-template-columns: repeat(2, 1fr);
    }
    
    ul#category {
        display: flex;
        width: 500px;
        justify-content: flex-start;
        list-style: none;
        flex-grow: 1;
    }
    
    li {
        margin-right: 20px;
        font-family: Racing Sans One;
        color: white;
        font-size: larger;
    }
    a#ex {
        background-image: url('../../img/category.png');
        background-size: 100% 100%;
    }
    
    .onHover {
        cursor: pointer;
    }
    
    div#currentPage {
        background-image: url('../../img/category.png');
        background-size: 100% 100%;
        animation: jello 2s infinite;
    }
}

