#contentLayout {
    position: absolute;
    display: grid;
    width: 80vw;
    top:100px;
    bottom: 100px;
    margin-left:10vw;
    margin-right:10vw;
    top:25%;
    grid-template-rows: 1fr;
    grid-template-columns: 1fr;
    grid-template-areas:
        "contentArea"
}

#contentLayoutAddJob {
    position: absolute;
    display: grid;
    width: 80vw;
    top:100px;
    bottom: 100px;
    margin-left:10vw;
    margin-right:10vw;
    vertical-align: center;
    top:20%;
    margin-bottom: 5%;
    overflow-x:hidden;
    grid-template-rows: 1fr;
    grid-template-columns: 1fr;
    grid-template-areas:
        "contentArea"
}

#contentDiv {
    position: absolute;
    display: grid;
    font-family:Jaldi;
    width: 100%;
    height: 100%;
}

#contentUserProfileDiv {
    background-image: url('../../img/rectangle_24.png');
    background-size: 100% 100%;
    position: absolute;
    display: grid;
    font-family:Jaldi;
    width: 100%;
    padding: 20px;
    bottom: 20px;
    height:100%;
}

#contentLayoutJobs {
    position: absolute;
    display: grid;
    width: 100vw;
    top:100px;
    bottom: 100px;
    overflow-x:hidden;
    grid-template-rows: 1fr;
    grid-template-columns: 1fr;
    grid-template-areas:
        "contentArea"
}

#contentDiv li {
    max-width: 80vw;
    word-wrap:break-word;
}

#contentDiv ul {
    max-width: 80vw;
}

/* #contentDiv .form-group{
    max-width: 500px;
} */

#contentLayoutUserProfile {
    position: absolute;
    display: grid;
    width: 80vw;
    top:100px;
    bottom: 100px;
    margin-left:10vw;
    margin-right:10vw;
    top:25%;
    overflow-x:hidden;
    grid-template-rows: 1fr;
    grid-template-columns: 1fr;
    grid-template-areas:
        "contentArea"
}

#addJobArea {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 10px;
    
}

#tagSelect {
    display:flex;
    justify-content: center;
}


#tagSelect select{
    min-width: 125px;
}



#contentDiv{grid-area: contentArea}