        margin-right: 20px;
        font-family: Racing Sans One;
        color: white;
        font-size: larger;
    }
    a#ex {
        background-image: url(data:image/png;base64,);
        background-size: 100% 100%;
    }
    
    .onHover {
        cursor: pointer;
    }
    
    div#currentPage {
        background-image: url(data:image/png;base64,);
        background-size: 100% 100%;
        -webkit-animation: jello 2s infinite;
                animation: jello 2s infinite;
    }
}

