#backgroundImg{grid-area: bgImg;}


ul#category {
    display: flex;
    justify-content: flex-start;
    list-style: none;
}
section#bottomtext div {
    position: absolute;
    bottom: 50px;
    width: 100%;
    height: 50px;
    border: 3px double rgb(0, 116, 183);
    overflow:hidden;
}
ul#bottom-display {
    padding: 0 40px;
    height: 50px;
    display: flex;
    list-style: none;
}

ul#bottom-display {
    position: relative;
    bottom: 0px;
    width:100%;
    overflow:hidden;
    -webkit-animation: slide-left 30s infinite;
            animation: slide-left 30s infinite;
}

ul#bottom-display li {
    font-size: 2.5vw;
    margin-left: 5vw;
    line-height: 50px;
}
  
@-webkit-keyframes slide-left {
    from {
      margin-left: 100%;
      width: 100%; 
    }
  
    to {
      margin-left: -100%;
      width: 100%;
    }
}
  
@keyframes slide-left {
    from {
      margin-left: 100%;
      width: 100%; 
    }
  
    to {
      margin-left: -100%;
      width: 100%;
    }
}



/* announcement styling (animation) */
.animated-text {
    border: 3px double rgb(0, 116, 183);
    color: black;
    font-family: Racing Sans One;
    -webkit-transform: translate(-50, -50%);
            transform: translate(-50, -50%);
    padding: 0 40px;
    height: 50px;
    overflow: hidden;
}
.line {
    text-transform: uppercase;
    text-align: center;
    width: 30vw;
    margin: auto;
    font-size: 30px;
    line-height: 50px;
}
.line:first-child {
    -webkit-animation: slide 12s infinite;
            animation: slide 12s infinite;
}

@-webkit-keyframes slide {
    0% {
        margin-top: 0px;
    }
    2% {
        margin-top: 0px;
    }
    16% {
        margin-top: -50px;
    }
    18% {
        margin-top: -50px;
    }
    33% {
        margin-top: -100px;
    }
    35% {
        margin-top: -100px;
    }
    66% {
        margin-top: -150px;
    }
    82% {
        margin-top: -200px;
    }
    100% {
        margin-top: -250px;
    }
}

@keyframes slide {
    0% {
        margin-top: 0px;
    }
    2% {
        margin-top: 0px;
    }
    16% {
        margin-top: -50px;
    }
    18% {
        margin-top: -50px;
    }
    33% {
        margin-top: -100px;
    }
    35% {
        margin-top: -100px;
    }
    66% {
        margin-top: -150px;
    }
    82% {
        margin-top: -200px;
    }
    100% {
        margin-top: -250px;
    }
}

